.Grid {
  padding: 5px;
  margin: 10px;
  background: #baa;
  width: 440px;
  margin: auto;
  border-radius: 7px;
}

.Grid-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Grid-tile {
  width: 100px;
  height: 100px;
  line-height: 100px;
  -ms-flex-align: center;
      align-items: center;
  border: solid 1px #ccc;
  border-radius: 7px;
  text-align: center;
  margin: 4px;
  font-size: 50px;
  font-weight: bold;
  color: #666;
  background: rgba(238, 228, 218, 0.35);
  -webkit-transition: background 0.3s, color .3s ease-in, font-size .5s ease-in-out;
  -o-transition: background 0.3s, color .3s ease-in, font-size .5s ease-in-out;
  transition: background 0.3s, color .3s ease-in, font-size .5s ease-in-out;
}

